import React from 'react';
import '../Styles/Contact.css';
import NavBar from './NavBar';
import emailjs from 'emailjs-com';
import { Pulse } from 'react-preloading-component';

class Contact extends React.Component {

    constructor(props) {
      super(props)
      this.state = {
        sent: false,
        complete: true,
        name: '',
        email: '',
        msg: '',
        loading: false
      }
      this.sendEmail = this.sendEmail.bind(this)
    }

    componentDidMount() {
      document.title = "Ryan Jacobson | Contact"
    }

    sendEmail(e) {
      e.preventDefault();
      if(this.state.name === '' || this.state.email === '' || this.state.msg === '') {
        this.setState({complete: false, sent: false})
        return
      }

      this.setState({loading: true})

      emailjs.sendForm('ryan_jacobson', 'template_js4fyoIk', e.target, 'user_yV0wX7e69aNa4WfCwcgdU')
        .then((result) => {
            console.log(result.text);
            this.setState({
              complete: true,
              sent: true,
              name: '',
              email: '',
              msg: '',
              loading: false
            })

          setTimeout(() => {
              this.setState({sent: false});
          }, 3000);

        }, (error) => {
            console.log(error.text);
        });
    }

    render() {
      return(
        <div>
          <NavBar />
          <h1 className="contact">CONTACT</h1>

          <form className="contact-form" onSubmit={this.sendEmail}>
            <label>Name</label>
            {!this.state.complete && !this.state.name && <p>Please fill out your name</p>}
            <input value={this.state.name} type="text" name="user_name" className="name" onChange={(e) => {
              this.setState({name: e.target.value})
            }} />
            <label>Email</label>
            {!this.state.complete && !this.state.email && <p>Please fill out your email</p>}
            <input value={this.state.email} type="email" name="user_email" className="email" onChange={(e) => {
              this.setState({email: e.target.value})
            }} />
            <label>Message</label>
            {!this.state.complete && !this.state.msg && <p>Please fill out your message</p>}
            <textarea value={this.state.msg} name="message" rows="3" className="msg" onChange={(e) => {
              this.setState({msg: e.target.value})
            }}></textarea>
            {this.state.sent && <p className="sent">Email Sent!</p>}
            <button type="submit" value="Send">{this.state.loading ? <div className="pulse"><Pulse color="white"/></div> : 'Send'}</button>
            <div className="socials">
              <a rel="noopener noreferrer" target="_blank" href="https://www.instagram.com/rv.an/"><i className="fa fa-instagram"></i></a>
              <a rel="noopener noreferrer" target="_blank" href="https://twitter.com/AtRyanAye"><i className="fa fa-twitter"></i></a>
              <a rel="noopener noreferrer" target="_blank" href="https://www.linkedin.com/in/ryan-jacobson-b11580191/"><i className="fa fa-linkedin"></i></a>
              <a rel="noopener noreferrer" target="_blank" href="https://github.com/jacobsonryan"><i className="fa fa-github"></i></a>
            </div>
          </form>
        </div>
      );
    }
  }
  
  export default Contact;