import React from 'react';
import NavBar from './NavBar';
import '../Styles/Projects.css';
import { Pulse } from 'react-preloading-component';

class Projects extends React.Component {

    constructor(props) {
      super(props)
      this.state = {
        items: [],
        loading: false
      }
    }

    componentDidMount() {
      document.title = "Ryan Jacobson | Projects"
      this.setState({loading: true})
      fetch('https://jacobsonryan.github.io/projects/#')
      .then(results => results.json())
      .then(results => this.setState({items: results, loading: false}))
    }

    render() {
      return(
        <div className="projects-background">
          <NavBar />
          <h1 className="project-title">PROJECTS</h1>
          <h1 className="project-loader">{this.state.loading ? <div className="pulse"><Pulse color="white"/></div> : ''}</h1>
          <div className="projects-container">
            {this.state.items.map((item, index) => {
              return(
                <div key={index} className="projects">
                  <div className="img-container"><img src={item.img} alt={item.title} width="1920" height="1080"/></div>
                  <li>{item.title}</li>
                  <p>{item.description}</p>
                  <div className="info">
                    <a rel="noopener noreferrer" target="_blank" className="project-button" href={item.code}>View GitHub</a>
                    <a rel="noopener noreferrer" target="_blank" className="project-button" href={item.view === '' ? null : item.view}>{item.view === '' ? null : "View Project"}</a>
                    <p className="date">Posted on {item.date}</p>
                  </div>
                </div>
              ) 
            })}
          </div>
        </div>
      );
    }
  }
  
  export default Projects;