import React from 'react';
import '../Styles/NavBar.css'
import { Link } from 'react-router-dom';
import HamburgerMenu from 'react-hamburger-menu'

class NavBar extends React.Component {

    constructor(props) {
      super(props);
      this.state = {isToggleOn: false, slide: false};

      this.handleClick = this.handleClick.bind(this);
      this.handleOutsideClick = this.handleOutsideClick.bind(this)
    }

    componentWillMount() {
      document.addEventListener('mousedown', this.handleOutsideClick, false)
    }

    componentWillUnmount() {
      document.removeEventListener('mousedown', this.handleOutsideClick, false)
    }

    handleOutsideClick(e) {
      if(!this.node.contains(e.target)) {
        this.setState({isToggleOn: false})
      }  
    }

    componentDidMount() {
      window.addEventListener('resize', () => {
        if (window.innerWidth >= 759) {
          this.setState({isToggleOn: false})
        }
      })
    }

    handleClick() {
      this.setState(state => ({
        isToggleOn: !state.isToggleOn
      }));
    }

    render() {
      return (
          <div className="navHolder">
            <ul ref={node => this.node = node} className={this.state.isToggleOn ? 'active' : ''} style={{backgroundColor: this.props.color}}>
                <h2 className="nav sub-name" style={{fontFamily: "Harriet, serif"}}><Link to="/">Ryan.</Link></h2>
                <li className="nav" style={{fontFamily: 'Balto-Med, sans-serif'}}><Link to="/">Home</Link></li>
                <li className="nav" style={{fontFamily: 'Balto-Med, sans-serif'}}><Link to="/about">About</Link></li>
                <li className="nav" style={{fontFamily: 'Balto-Med, sans-serif'}}><Link to="/projects">Projects</Link></li>
                <li className="nav" style={{fontFamily: 'Balto-Med, sans-serif'}}><Link to="/contact">Contact</Link></li>
                <div className="burger">
                  <HamburgerMenu
                      isOpen={this.state.isToggleOn}
                      menuClicked={this.handleClick}
                      width={20}
                      height={14}
                      strokeWidth={1.5}
                      color='#f2f2f2'
                      animationDuration={0.5}
                  />
                </div>
            </ul>
          </div>
      );
    }
  }
  
  export default NavBar;