import React from 'react';
import NavBar from './NavBar';
import '../Styles/About.css';
import { Link } from 'react-router-dom';

class About extends React.Component {

    constructor(props) {
      super(props)
      this.state = {
        skills: true
      }
      this.techSkills = this.techSkills.bind(this)
      this.appSkills = this.appSkills.bind(this)
    }

    componentDidMount() {
      document.title = "Ryan Jacobson | About"
    }

    techSkills() {
      this.setState({
        skills: true
      })
    }

    appSkills() {
      this.setState({
        skills: false
      })
    }

    render() {
      return(
        <div>
          <NavBar />
          <div className="about-container">
            <h1 className="about-title">ABOUT</h1> 
            <div className="about">
                  <p className="drop-caps">My name is Ryan Jacobson and I am a full stack web developer. My interest in web development and programming has led me to many opportunities including an internship at J.P. Morgan Chase where I gained hands on experience in a corporate environment building applications while learning concurrently. I look forward to using my passion for computer science to build beautiful, fully scalable applications.
                  </p>
              </div>
          </div>
          <div className="skills-title">
            <h2 className={'tech', this.state.skills ? 'clicked' : 'tech'} onClick={this.techSkills} style={{color: this.state.skills ? '#f2f2f2' : '#b3b3b3', fontFamily: 'Balto-Med'}}>Technical Skills</h2>
            <h2 className={this.state.skills ? 'app' : 'clicked'} onClick={this.appSkills} style={{color: this.state.skills ? '#b3b3b3' : '#f2f2f2', fontFamily: 'Balto-Med'}}>Application Skills</h2>
          </div>
            <div className="skills">
              <div className={this.state.skills ? 'show' : 'remove'}>
                <ul>
                  <li className="techSkills"><i style={{color: "#ff1717"}} className="fab fa-adobe"></i>&nbsp;Adobe Creative Suite</li>
                  <li className="techSkills"><i style={{color: "#17aaff"}} className="fab fa-microsoft"></i>&nbsp;Microsoft Office</li>
                </ul>
              </div>
              <div className={this.state.skills ? 'remove' : 'show'}>
                <ul>
                  <li className="appSkills"><i style={{color: "#ff742e"}} className=" fab fa-html5"></i>&nbsp;HTML</li>
                  <li className="appSkills"><i style={{color: "#3aa2fc"}} className=" fab fa-css3-alt"></i>&nbsp;CSS</li>
                  <li className="appSkills"><i style={{color: "#fce93a"}} className=" fab fa-js"></i>&nbsp;JavaScript</li>
                  <li className="appSkills"><i style={{color: "#0071d4"}} className=" fab fa-python"></i>&nbsp;Python</li>
                  <li className="appSkills"><i style={{color: "#ff1717"}} className=" fab fa-java"></i>&nbsp;Java</li>
                  <li className="appSkills"><i style={{color: "#17aaff"}} className=" fab fa-react"></i>&nbsp;React</li>
                </ul>
              </div>
            </div>
        </div>
      );
    }
  }
  
  export default About;


